.subHeading {
    font-size: smaller;
    color: black;
    font-weight: bold;
}

.metrics {
    text-align: center;
}

.metricsHeading {
    font-size: smaller;
    color: black;
    font-weight: bold;
    margin: auto;
    padding: 2%;
}

.metricsVariable {
    width: fit-content;
}

.main-heading {
    display: grid;
    align-items: center;
    justify-content: space-around;
}

#select-model-version-label, #training-data-version-label {
    font-size: small;
    color: black;
}

#select-model-version, #training-data-version {
    font-size: small;
    color: black;
}

.formSelect {
    margin-bottom: 2%;
}

.customeSelect {
    background-color: #ffffff;
    color: #000000;
    height: 40px;
    text-align: center;
    margin-bottom: 2%;
}

.main-variables {
    display: flex;
    align-items: flex-end;
}

.variableName {
    color: black;
    font-size: 12px;
}

.variableValue {
    box-shadow: 0px 2px 2px -1px rgba(0, 0, 0, 0.25);
    background-color: #d9d9d9;
    padding: 3%;
    width: 100px;
    font-weight: 600 !important;
    color: black !important;
}