.chartMain {
    justify-content: space-between;
}

.mainChartDiv {
    background-color: #f5f5f5;
    border-radius: 20px;
    padding: 2%;
}

.chartsDiv {
    background-color: #d9d9d9;
    border-radius: 20px;
    padding: 3%;
    margin: 2%;
}

.chartHeadMain {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.chartHeading {
    height: fit-content;
    padding: 2%;
    background: rgba(255, 255, 255, 0.4);
    color: #000000;
    border-radius: 3px;
    font-weight: 600 !important;
    font-size: smaller !important;
    width: 100px;
}

.chartHeadingIcon {
    padding: 0 5px;
}