.slogan {
    padding: 3%;
}

.loginHeading {
    text-align: left;
    padding: 3%;
    width: 100%;
}

input[type="email"] {
    width: 350px;
    height: 60px;
    border-radius: 3px;
    outline: none;
    box-sizing: border-box;
    box-shadow: 2px 2px 1px rgba(181, 181, 181, 0.25);
}

input[type="password"] {
    width: 350px;
    height: 60px;
    border-radius: 3px;
    outline: none;
    box-sizing: border-box;
    box-shadow: 2px 2px 1px rgba(181, 181, 181, 0.25);
  }

input[type="text"]:focus {
    border-color: dodgerBlue;
    box-shadow: 0 0 8px 0 dodgerBlue;
}

.rememberMeCheckbox {
    color: #525252;
}

#rememberMe {
    color: #a1a1a1;
    font-size: smaller;
}

#forgotPwd {
    text-decoration: none;
    font-size: small;
    font-weight: 600;
    color: #525252;
}

.loginButton {
    background: #1F92C0;
    box-shadow: 2px 2px 1px rgba(181, 181, 181, 0.25);
    color: #fff;
    border-radius: 8px;
    width: 100%;
    margin: 0 10%;
    border: none;
    padding: 2%;
}

#notRegisteredText {
    font-size: medium;
    color: #828282;
    font-weight: 400;
    text-decoration: none;
}

#createAccount {
    font-size: medium;
    color: #353535;
    font-weight: 600;
    text-decoration: none;
}